import IMathObject, { MatrixData, ScalarData } from "./IMathObject";
import MathObjectType from "./MathObjectType";
import Matrix from "../Matrix";
import MatrixObject from "./MatrixObject";

class ScalarObject implements IMathObject{
    name: string;
    type: MathObjectType = MathObjectType.SCALAR;
    value: ScalarData;

    constructor(data: ScalarData, name?: string){
        if(typeof data === 'string'){
            data = parseFloat(data);
        }
        this.value = data;
        this.name = name ?? data.toFixed(2);
    }

    add: (other: IMathObject) => IMathObject = (other: IMathObject) => {
        if(other.type === MathObjectType.SCALAR)
            return new ScalarObject(this.value + (other.value as ScalarData), this.name + " + " + other.name);
        return new MatrixObject((other.value as MatrixData).getMatrix().plus(this.value), this.name + " + " + other.name);
    };
    
    subs: (other: IMathObject) => IMathObject = (other: IMathObject) => {
        if(other.type === MathObjectType.SCALAR)
            return new ScalarObject(this.value - (other.value as ScalarData), this.name + " - " + other.name);
        return new MatrixObject((other.value as MatrixData).getMatrix().minus(this.value), this.name + " - " + other.name);
    }

    div: (other: IMathObject) => IMathObject = (other: IMathObject) => {
        if(other.type === MathObjectType.SCALAR)
            return new ScalarObject(this.value / (other.value as ScalarData), this.name + " / " + other.name);
        return new MatrixObject((other.value as MatrixData).getMatrix().divide(this.value), this.name + " / " + other.name);
    }

    mult: (other: IMathObject) => IMathObject = (other: IMathObject) => {
        if(other.type === MathObjectType.SCALAR)
            return new ScalarObject(this.value * (other.value as ScalarData), this.name + " * " + other.name);
        return new MatrixObject((other.value as MatrixData).getMatrix().scale(this.value), this.name + " * " + other.name);
    }
    
    uminus: () => IMathObject = () => {
        return new ScalarObject(-this.value, "-" + this.name);
    }
}

export default ScalarObject;