import IMathObject, { MatrixData, ScalarData } from "./IMathObject";
import MathObjectType from "./MathObjectType";
import Matrix from "../Matrix";

class MatrixObject implements IMathObject{
    name: string;
    type: MathObjectType = MathObjectType.MATRIX;
    value: MatrixData;
    matrix: Matrix;

    constructor(data: MatrixData | Matrix, name: string){
        if(data instanceof Matrix){
            this.value = {
                cols: data.size.x,
                rows: data.size.y,
                values: data.values,
                getMatrix: () => this.matrix
            }
            this.matrix = data.clone();
        }else{
            this.value = {
                cols: data.cols,
                rows: data.rows,
                values: data.values,
                getMatrix: () => this.matrix
            }
            this.matrix = new Matrix(data.values, [data.rows, data.cols])
        }
        this.name = name;
    }

    add: (other: IMathObject) => IMathObject = (other: IMathObject) => {
        if(other.type === MathObjectType.SCALAR)
            return new MatrixObject(this.matrix.plus((other.value as ScalarData)), this.name + " + " + other.name);
        else
            return new MatrixObject(this.matrix.plus((other.value as MatrixData).getMatrix()), this.name + " + " + other.name);
    };
    
    subs: (other: IMathObject) => IMathObject = (other: IMathObject) => {
        if(other.type === MathObjectType.SCALAR)
            return new MatrixObject(this.matrix.plus((other.value as ScalarData)), this.name + " - " + other.name);
        else
            return new MatrixObject(this.matrix.plus((other.value as MatrixData).getMatrix()), this.name + " - " + other.name);
    }

    div: (other: IMathObject) => IMathObject = (other: IMathObject) => {
        if(other.type === MathObjectType.SCALAR)
            return new MatrixObject(this.matrix.mult((other.value as ScalarData)), this.name + " / " + other.name);
        else 
            throw new Error();
    }

    mult: (other: IMathObject) => IMathObject = (other: IMathObject) => {
        if(other.type === MathObjectType.SCALAR)
            return new MatrixObject(this.matrix.mult((other.value as ScalarData)), this.name + " * " + other.name);
        else
            return new MatrixObject(this.matrix.mult((other.value as MatrixData).getMatrix()), this.name + " * " + other.name);
    }
    
    uminus: () => IMathObject = () => {
        return new MatrixObject(this.matrix.mult(-1), "-" + this.name);
    }
}

export default MatrixObject;